.classcentral-style .table {
  box-sizing: border-box;
  width: 100%;
  table-layout: fixed;
  border: none;
  margin-bottom: 0;
  /* table truncate
  ..................................*/
  /* table head
  ..................................*/
  /* table body
  ..................................*/
  /* table striped
  ..................................*/
  /* table small
