  /* table block
  ..................................*/
}
.classcentral-style .table.border-top {
  border-bottom: 1px solid #F2F2F2;
}
.classcentral-style .table.table--auto {
  width: auto;
}
.classcentral-style .table.table--truncate td,
.classcentral-style .table.table--truncate th {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.classcentral-style .table.table--ticks th {
  position: relative;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.classcentral-style .table.table--ticks th:before {
  content: " ";
  height: 10px;
  width: 1px;
  background: #F2F2F2;
  position: absolute;
  bottom: 0;
  right: 0;
}
.classcentral-style .table.table--ticks th:last-child:before {
  display: none;
}
.classcentral-style .table.table--ticks .table--ticks .no--tick:before {
  display: none;
}
.classcentral-style .table thead,
.classcentral-style .table th {
  box-sizing: border-box;
}
.classcentral-style .table thead {
  border: none;
  font-weight: bold;
  background: transparent;
}
.classcentral-style .table thead tr {
  border-bottom: 1px solid #F2F2F2;
}
.classcentral-style .table thead th {
  padding: 15px 15px 15px 20px;
  border: none;
}
.classcentral-style .table thead th:first-child {
  background: none;
}
.classcentral-style .table th,
.classcentral-style .table td {
  box-sizing: border-box;
  padding: 20px 15px;
  vertical-align: top;
}
.classcentral-style .table tbody {
  box-sizing: border-box;
}
.classcentral-style .table tr {
  box-sizing: border-box;
  background: transparent;
  border-bottom: 1px solid #F2F2F2;
  border-left: 3px solid tranparent;
}
.classcentral-style .table tr.clickable {
  cursor: pointer;
}
.classcentral-style .table tr:nth-child(even) {
  background: transparent;
}
.classcentral-style .table.table--striped tbody tr {
  border: none;
}
.classcentral-style .table.table--striped tbody tr:nth-child(odd) {
  background: #FAFBFB;
}
.classcentral-style .table.table--small th,
.classcentral-style .table.table--small td {
  padding: 10px;
  height: auto;
}
.classcentral-style .table.table--block thead,
.classcentral-style .table.table--block th,
.classcentral-style .table.table--block tr,
.classcentral-style .table.table--block td {
  display: block;
}
.classcentral-style .table.table--block th,
.classcentral-style .table.table--block td {
  float: left;
}
.classcentral-style .table.table--block thead,
.classcentral-style .table.table--block tr {
  width: 100%;
}
.classcentral-style .table.table--block thead:before,
.classcentral-style .table.table--block tr:before,
.classcentral-style .table.table--block thead:after,
.classcentral-style .table.table--block tr:after {
  content: " ";
  display: table;
}
.classcentral-style .table.table--block thead:after,
.classcentral-style .table.table--block tr:after {
  clear: both;
}
.classcentral-style .firefox .table .table-row-head .table-row-arrow {
  bottom: 1px;
}



