.classcentral-style .horz-rule {
  width: 100%;
  margin: 0;
  height: 1px;
  border-bottom: 2px solid #F2F2F2;
}
.classcentral-style .vert-rule {
  position: relative;
  border: none;
  margin: 0;
  clear: none;
  display: block;
  min-width: 1px;
  border-width: 0;
  min-height: 1px;
}
.classcentral-style .vert-rule:before {
  content: " ";
  display: block;
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  height: 100%;
  width: 2px;
  background: #F2F2F2;
}
.classcentral-style .vert-rule.border--thin:before {
  width: 1px;
}
.classcentral-style .vert-rule.border--think:before {
  width: 3px;
}
