.classcentral-style {
  /*.....................................
  CHECKBOXES
.....................................*/
}
.classcentral-style .checkbox {
  display: inline-block;
  line-height: 0;
  vertical-align: middle;
  width: 100%;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.classcentral-style .checkbox label .checkbox--icon {
  position: absolute;
  top: 0;
  left: 0;
  display: inline-block;
  width: 15px;
  height: 15px;
  border: 1px solid #F2F2F2;
  background: #ffffff;
  -webkit-transition: all 300ms ease;
  -moz-transition: all 300ms ease;
  -o-transition: all 300ms ease;
  transition: all 300ms ease;
}
.classcentral-style .checkbox label .checkbox--icon.checkbox--icon-solid {
  padding: 2px;
  border-radius: 5px;
  background: #ffffff;
  border: 1px solid #DEDEDE;
}
.classcentral-style .checkbox label .checkbox--label {
  line-height: 18px;
  margin-left: 24px;
}
.classcentral-style .checkbox label .checkbox--label.checkbox--label-solid {
  line-height: 24px;
  margin-left: 28px;
}
.classcentral-style .checkbox label:hover .checkbox--icon {
  border: 1px solid #F2F2F2;
  background: #ffffff;
  cursor: pointer;
}
.classcentral-style .checkbox input[type=checkbox] {
  display: none;
}
.classcentral-style .checkbox input[type=checkbox] + label .icon-check {
  display: none;
}
.classcentral-style .checkbox input[type=checkbox]:checked + label {
  background-color: #ffffff;
}
.classcentral-style .checkbox input[type=checkbox]:checked + label .checkbox--icon-solid {
  background: #5074E9;
  border: 1px solid #5074E9;
}
.classcentral-style .checkbox input[type=checkbox]:checked + label .checkbox--icon-solid .icon {
  top: -6px;
  left: -5px;
}
.classcentral-style .checkbox input[type=checkbox]:checked + label .icon {
  position: absolute;
  top: -8px;
  left: -6px;
  background-size: contain;
  height: 30px;
  width: 30px;
  display: block;
}
.classcentral-style .checkbox--toggle {
  display: inline-block;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.classcentral-style .checkbox--toggle label {
  display: inline-block;
  width: 35px;
  border: 1px solid #F2F2F2;
  height: 19px;
  border-radius: 30px;
  position: relative;
}
.classcentral-style .checkbox--toggle label:hover {
  cursor: pointer;
}
.classcentral-style .checkbox--toggle input[type=checkbox] {
  display: none;
}
.classcentral-style .checkbox--toggle input[type=checkbox] + label .disc {
  border: 1px solid #F2F2F2;
  background: #F2F2F2;
  left: 4px;
}
.classcentral-style .checkbox--toggle input[type=checkbox]:checked + label .disc {
  left: 17px;
  background: #8BE2BB;
  border: 1px solid #3ECF8E;
}
.classcentral-style .checkbox--toggle .disc {
  -webkit-transition: left 200ms ease;
  -moz-transition: left 200ms ease;
  -o-transition: left 200ms ease;
  transition: left 200ms ease;
  width: 13px;
  height: 13px;
  display: block;
  border-radius: 50%;
  position: absolute;
  top: 2px;
}
.classcentral-style .checkbox--large {
  height: 29px;
}
.classcentral-style .checkbox--large label {
  width: 50px;
  height: 27px;
  border-radius: 50px;
}
.classcentral-style .checkbox--large input[type=checkbox]:checked + label .disc {
  left: 25px;
}
.classcentral-style .checkbox--large .disc {
  width: 19px;
  height: 19px;
  top: 3px;
}



