.loading-pulse {
  position: relative;
  width: 4.5px;
  height: 18px;
  background: rgba(0, 0, 0, 0.3);
  animation: pulse 750ms infinite;
  animation-delay: 250ms;
}
.loading-pulse:before,
.loading-pulse:after {
  content: '';
  position: absolute;
  display: block;
  height: 12px;
  width: 4.5px;
  background: rgba(0, 0, 0, 0.3);
  top: 50%;
  transform: translateY(-50%);
  animation: pulse 750ms infinite;
}
.loading-pulse:before {
  left: -9px;
}
.loading-pulse:after {
  left: 9px;
  animation-delay: 500ms;
}
