[data-button-follow] button {
  background-color: #043BFF;
  color: #ffffff;
  font-family: "brandon-grotesque", "Brandon Grotesque", Helvetica, Arial, sans-serif;
  transition: border 300ms cubic-bezier(0.19, 1, 0.22, 1), color 300ms cubic-bezier(0.19, 1, 0.22, 1), opacity 300ms cubic-bezier(0.19, 1, 0.22, 1), background-color 300ms cubic-bezier(0.19, 1, 0.22, 1), width 300ms cubic-bezier(0.19, 1, 0.22, 1);
  border: 1px solid #043BFF;
  display: inline-block;
  position: relative;
  box-sizing: border-box;
  text-align: center;
  line-height: 38px;
  height: 40px;
  padding: 0 75px 0px 35px;
  outline: none;
  border-radius: 5px;
  width: 205px;
}
[data-button-follow] button .tooltip {
  position: absolute;
  left: 0;
  opacity: 0;
  top: 43px;
  width: 205px;
  text-align: left;
  box-sizing: border-box;
  padding: 15px 20px;
  background: white;
  border: 1px solid #D1D1D1;
  border-radius: 5px;
  pointer-events: none;
  visibility: hidden;
  transition: top 300ms ease, opacity 300ms ease;
  z-index: 0;
}
[data-button-follow] button .tooltip .head-5 {
  color: #001048;
  font-size: 16px;
  font-weight: 400;
  margin: 0 0 3px 0;
  line-height: 18px;
}
[data-button-follow] button .tooltip .text-4 {
  color: #95989A;
  font-size: 14px;
  font-weight: 300;
  line-height: 18px;
  margin: 0;
}
[data-button-follow] button .tooltip:before,
[data-button-follow] button .tooltip:after {
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-color: rgba(255, 255, 255, 0);
  bottom: 100%;
  left: 50%;
  border-bottom-color: #ffffff;
}
[data-button-follow] button .tooltip:before {
  margin-left: -7px;
  border-width: 7px;
  border-bottom-color: #D1D1D1;
}
[data-button-follow] button .tooltip:after {
  margin-left: -6px;
  border-width: 6px;
